body{
  margin: 0;
  padding: 0;
  background: radial-gradient(circle at center, #0f0f0f 0%, #000000 100%);
  color: #00ffcc;
  font-family: 'Orbitron', sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@500&display=swap');

.App{
  text-align: center;
  background-color: rgba(10,10,30,0.8);
  border: 2px solid #00ffcc;
  border-radius: 20px;
  padding: 30px;
  box-shadow: 0 0 40px #00ffcc, 0 0 60px #007766;
  max-width: 500px;
  width: 90%;
}

h1{
  font-size: 2rem;
  color: #66ffe0;
  text-shadow: 0 0 10px #00ffcc;
  margin-bottom: 20px;
}

#session-label, #Session-label{
  margin: 20px 0;
  font-size:  1.2rem;
}

button{
  margin: 0 10px;
  padding: 10px 20px;
  font-family: 'Orbitron', sans-serif;
  font-size: 1rem;
  background: transparent;
  border: 2px solid #00ffcc;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.3s ease;
  text-shadow: 0 0 5px #00ffcc;
}

button:hover{
  background-color: #00ffcc;
  color: #000;
  box-shadow: 0 0 10px #00ffcc;
}

#timer-label{
  font-size: 2rem;
  color: #ffffff;
  margin-top: 30px;
  text-shadow: 0 0 5px #00ffcc;
}

#time-left{
  font-size: 3rem;
  margin: 15px 0;
  color: #00ffcc;
  text-shadow: 0 0 5px #00ffcc;
}

#start_stop, #reset{
  margin: 10px;
  padding: 10px 15px;
  min-width: 50px;
  font-size: 1.2rem;
  color: #00ffff;
}
#start_stop:hover, #reset:hover{
  color: #000000;
}
audio{
  display: none;
}

#break-decrement,
#break-increment,
#session-decrement,
#session-increment{
  padding: 10px 15px;
  min-width: 50px;
  font-size: 1.2rem;
  color: #00ffff;
}

#break-decrement:hover,
#break-increment:hover,
#session-decrement:hover,
#session-increment:hover{
  color: #000;
}